body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{text-align:center;width:100%;height:100%}.main-body{margin:1%}
.search-row{height:55px}.search-form{height:100%}.search-input{height:100%!important;width:100%;border-width:1px;font-size:17pt!important}.invalid-search-alert{text-align:left}.search-button-col{padding-left:0!important}.search-button{height:100%;width:100%;background-color:#22975e!important;border-color:#22975e!important}.search-button-icon{height:90%}
.main-header{height:130px;margin-top:1%;margin-bottom:1%;background:#1e5995;background:-webkit-linear-gradient(left,rgba(30,89,149,1) 0,rgba(34,151,94,1) 100%);background:linear-gradient(90deg,rgba(30,89,149,1) 0,rgba(34,151,94,1) 100%)}.main-header-col{margin:auto}.main-header-text{font-weight:700;color:#fff;font-size:30pt;margin:0!important}
.waste-items-table{margin-top:.7rem;width:99%}.empty-list-alert{margin-top:1%;font-size:15pt;color:#6E757D!important}.waste-items-row{padding-top:1.5%;padding-bottom:1.5%}.waste-item-title-row{margin-left:1%!important}.star-button{background-color:Transparent!important;background-repeat:no-repeat!important;border:none!important;cursor:pointer!important;overflow:hidden!important;outline:0!important;padding:0!important}.green-star{margin-top:auto;margin-bottom:auto}.waste-item-title{font-size:16pt;text-align:left;margin-left:3%;margin-bottom:0}.waste-item-instructions{font-size:15pt;text-align:left}
.favourites-header,.favourites-list-row{background:#f7fefa}.favourites-header{text-align:left}.favourites-header-text{font-weight:700;color:#22975e;font-size:25pt;margin-left:.2%!important;margin-top:1%}.favourite-items-table{margin-top:.7rem}.empty-list-alert{margin-top:1%;font-size:15pt;color:#6E757D!important}.favourite-item-row{padding-top:1.5%;padding-bottom:1.5%}.favourite-title-row{margin-left:1%!important}.star-button{background-color:Transparent!important;background-repeat:no-repeat!important;border:none!important;cursor:pointer!important;overflow:hidden!important;outline:0!important;padding:0!important}.favourite-item-title{font-size:16pt;text-align:left;margin-left:3%;margin-bottom:0}.favourite-item-instructions{font-size:15pt;text-align:left}
